<template>
  <div id="head">
    <ul>
      <li v-for = "item in arr"  :key= "item.id">
        <i :class = "[item.icon, {headActive:item.id == id}]" :title= "item.name" @click = "Popup(item.id)"></i>
        <div class = "headPopup" :class = "{'active': item.id==id}" v-if = "item.title">
            <div v-if = "item.name == '简介'" class = "headPopupContent">
              <div>1</div>
              <p>{{item.title.name}}</p>
              <p>{{item.title.content}}</p>
            </div>

            <div v-else-if = "item.name == '搜索'" class = "headPopupContent">
              <input type="text" :placeholder = "item.title.content"/>
            </div>

            <div v-else></div>
        </div>
      </li>
    </ul>
    
  </div>
</template>

<script>
export default {
  name: 'HeadPage',
  data () {
    return {
      arr:[
        {
          name:'简介',
          icon:'iconfont icon-viewgallery',
          id:0,
          title:{
            name:'vonne',
            content:'大介好，我是vonne，几需体验三番钟，你就会干我一样爱上介个博客，点一厦，看一年，不要咸。'
          }

        },{
          name:'搜索',
          icon:'iconfont icon-search',
          id:1,
          title:{
            content:'文章搜索'
          }
        },{
          name:'搜索',
          icon:'iconfont icon-search',
          id:2,
          title:{
            content:'文章搜索'
          }
        },{
          name:'搜索',
          icon:'iconfont icon-search',
          id:3,
           title:{
            content:'文章搜索'
          }
        },{
          name:'搜索',
          icon:'iconfont icon-search',
          id:4  
        }
      ],
      id:-1
     
    }
  },

  methods:{
    
    //确认弹出窗
    Popup(id){ 

      if(id == this.id){
        this.id = -1
      }else{
        this.id = id
      }
      
    }
   
  },

}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
  #head{
    text-align: center;
    position: fixed;
    z-index:98;
    top:50%;
    right:0;
    margin-top:-300px;
    width:7%;
    height:600px;
    background:black;
    ul{
      display: flex;
      flex-direction:column;
      justify-content: space-around;
      height:100%;
      li{
        top:-60px;
        position: relative; 
        cursor: pointer;
        i{
          line-height: 120px;
          position: absolute;
          z-index: 99;
          background: black;
          left:0;
          right:0;
          color:#737373;
          font-size:35px;
        }
        &:hover i{
          background:#5f5d5d; 
          color:#fff;       
        }
        .headPopup{
          border-radius: 6px 0 0 6px;
          position: absolute;
          width:240px;
          height:auto;
          min-height:120px;
          left:0;
          top:0;
          background: rgba(0,0,0,.6);
          transition: left 0.5s;
          .headPopupContent{
            line-height: 120px;
            color:#fff;
            text-align: center;
            div{
              margin:10px auto;
              width:70px;
              height:70px;
              border-radius: 50%;
              background:#fff;
              
            }
            p{
              line-height: 20px;
              margin:5px 20px;
            }
            input{
              border:1px solid #ddd;
              width:178px;
              height:28px;
              border-radius: 4px;
              outline: none;
            }
            
          }
        }
        .active{
          left:-240px;        
        }
        .headActive{
          background:#5f5d5d;
          color:#fff;
        }
      }
    }
  
    
    
  }
</style>



