<template>
    <div id="contentbox">
        <div class="listbox">
            <ul class= "list">
                <li v-for = "(list,i) in contentList" :key = "i">
                    <span><img :src="list.img" /></span>
                    <h3>{{list.title}}</h3>
                    <p>{{list.brief}}</p>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
import '../../../static/lib/jquery.corner';
import '../../../static/lib/jquery.roundabout';
import '../../../static/lib/jquery.roundabout-shapes';
export default {
    name : 'ContentPage',

    data () {
        return {
            contentList : [
                {
                    img : '图片',
                    title : '标题',
                    brief : '这是一个标题',
                    content : '内容内容'
                },
                {
                    img : '图片',
                    title : '标题',
                    brief : '这是一个标题',
                    content : '内容内容'
                },
                {
                    img : '图片',
                    title : '标题',
                    brief : '这是一个标题',
                    content : '内容内容'
                },
                {
                    img : '图片',
                    title : '标题',
                    brief : '这是一个标题',
                    content : '内容内容'
                },
                {
                    img : '图片',
                    title : '标题',
                    brief : '这是一个标题',
                    content : '内容内容'
                }
            ]
        }
    },
    mounted () {
        //动画
        $('.list').roundabout({
			minOpacity:1,
			btnNext: ".next",
			duration: 1000,
			reflect: true,
			btnPrev: '.prev',
			tilt:0,
			shape: 'figure8'
		});
    }
}
</script>

<style lang="scss" scoped>
    #contentbox{
        width:93%;
        height:100%;
         position: fixed;
        .listbox{
            width:800px;
            height:600px;
            position: relative;
            top:50%;
            left:50%;
            margin-left:-400px;
            margin-top:-300px;
            .list{
                top:50%;
                margin-top:-300px;
                position: relative;
                li{
                    padding:20px 20px;
                    border-radius: 6px;
                    cursor: pointer;
                    text-align: center;
                    position:absolute;
                    width:460px;
                    height:560px;
                    background:rgba(0,0,0,.6);
                    span{
                        width:100%;
                        height:40%;
                        background:#fff;
                        display: inline-block;
                        margin:0 auto;
                    }
                    h3{
                        margin:30px 0;
                    }
                }
            }
        }
    }
</style>




